.nav {
  display: none;

  @include tablet {
    display: block;
    padding-bottom: 2px;
  }
}

.nav__list {
  @extend %reset_default_styles_list;
  display: grid;
  row-gap: 40px;
  align-items: center;
  justify-items: center;
  font-size: 32px;
  line-height: 160%;

  @include tablet {
    display: grid;
    grid-template-columns: repeat(4, auto);
    column-gap: 36px;
    row-gap: 10px;
    align-items: stretch;
    font-size: 15px;
  }
}

.nav__link {
  display: block;
  box-sizing: border-box;
  text-decoration: none;
  transition-duration: 0.3s;
  padding-bottom: 3px;
  overflow-x: hidden;

  &--current {
    position: relative;
    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: $desert_sand;
    }

    &:hover {
      &::before {
        transition-duration: 0.2s;
        transform: translateX(-100%);
      }
    }

  }

  &--main-page {
    color: $light_telegray;
  }

  &--main-page-current {
    color: $light_white;
  }

  &--main-page:hover {
    color: $light_white;
  }

  &--our-pets {
    color: $asphalt_color;
  }

  &--our-pets-current {
    color: $signal_black;
  }

  &--our-pets:hover {
    border-color: transparent;
    color: $signal_black;
  }
}
