.button {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;

  font-family: 'Georgia';
  font-style: normal;
  font-weight: 400;
  color: $signal_black;
  text-decoration: none;
  border: none;
  border-radius: 100px;
  background-color: transparent;
  cursor: pointer;
  transition-duration: 0.3s;

