.slider {
  display: grid;
  position: relative;
  width: 270px;

  @include tablet {
    width: 708px;
  }

  @include desktop {
    width: 1200px;
  }
}

.slider__wrapper {
  overflow: hidden;
  z-index: 5;

  @include tablet {
    margin: 0 64px;
  }

  @include desktop {
    margin: 0 105px;
  }
}

.slider__list {
  @extend %reset_default_styles_list;
  display: flex;
  transition: transform 0.6s ease;
  column-gap: 0;

  @include tablet {
    column-gap: 40px;
  }

  @include desktop {
    column-gap: 90px;
  }
}

.slider__item {
  flex-shrink: 0;
}

.slider__controls {
  display: flex;
  justify-content: space-between;
  padding: 20px 43px 0;

  @include tablet {
    position: absolute;
    left: 0;
    top: 50%;
    padding: 0;
    width: 100%;
    transform: translate(0, -50%);
    z-index: 3;
  }

}

.slider__button {
  width: 52px;
  height: 52px;
  border: 2px solid $desert_sand;

  &--next {
    transform: rotate(180deg);
  }

  &--active:hover {
    border-color: $apricot_criola;
    background-color: $apricot_criola;
  }
}
