@keyframes showPopup {
  0%{
    transform: translate(-50%, -50%) scale(0) ;
  }
  100%{
    transform:translate(-50%, -50%) scale(1) ;
  }
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  animation-name:showPopup;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  width: 240px;
  background: $light_white;
  border-radius: 9px;
  font-family: 'Georgia';
  z-index: 60;

  @include tablet {
    width: 630px;
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  @include desktop {
    width: 900px;
  }
}

.popup__content {
  height: min-content;
  margin: 10px;
  overflow: auto;

  @include tablet {
    width: 261px;
  }

  @include desktop {
    width: 351px;
    margin: 50px 20px 0;
  }
}

.popup__title {
  @extend %title;

  text-align: center;
  margin-bottom: 10px;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;

  @include tablet {
    text-align: start;
  }
}

.popup__subtitle {
  @extend %title;

  text-align: center;
  margin-bottom: 20px;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;

  @include tablet {
    text-align: start;
  }

  @include desktop {
    margin-bottom: 40px;
  }
}

.popup__description {
  margin: 0;
  margin-bottom: 24px;
  font-size: 13px;
  line-height: 113%;
  text-align: justify;
  letter-spacing: 0.06em;

  @include tablet {
    text-align: start;
    margin-bottom: 20px;
  }

  @include desktop {
    font-size: 15px;
    margin-bottom: 41px;
  }
}

.feature-list {
  @extend %reset_default_styles_list;

  display: grid;
  row-gap: 6px;

  @include desktop {
    row-gap: 11px;
  }
}

.feature-list__item-wrapper {
  position: relative;
  padding-left: 15px;
  display: flex;

  &::before {
    position: absolute;
    top: 6px;
    left: 0;
    content: '';
    width: 4px;
    height: 4px;
    background-color: $desert_sand;
    border-radius: 50%;
  }
}

.feature-list__term {
  font-family: Georgia;
  font-weight: 700;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
  white-space: pre;
}

.feature-list__description {
  @extend %title;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
}

.popup__img {
  display: none;

  @include tablet {
    display: block;
    border-top-left-radius: 9px;
    border-bottom-left-radius: 9px;
  }

  @include desktop {
    width: 500px;
  }
}

.popup__button-close {
  position: absolute;
  top: -52px;
  right: -30px;
  width: 52px;
  height: 52px;
  border: 2px solid $desert_sand;

  &--active:hover {
    border-color: $apricot_criola;
    background-color: $apricot_criola;
  }

  @include tablet {
    right: -43px;
  }

}
