.card {
  display: grid;
  padding-bottom: 30px;
  justify-items: center;
  row-gap: 30px;

  background-color: $light_white;
  border-radius: 9px;
  cursor: pointer;
  transition-duration: 0.3s;

  &:hover {
    box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
    background-color: white;
    transform: scale(0.99);
  }

  &:hover .card__button {
    border-color: $apricot_criola;
    background-color: $apricot_criola;
  }
}

.card__img {
  will-change: transform;
  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
}

.card__title {
  @extend %title;

  font-size: 20px;
  line-height: 23px;
  color: $asphalt_color;
}

.card__button {
  padding: 13px 43px;

  font-size: 17px;
  line-height: 130%;
  letter-spacing: 0.06em;
  border: 2px solid $desert_sand;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    border-color: $apricot_criola;
    background-color: $apricot_criola;
  }
}
