.pets__list {
  @extend %reset_default_styles_list;
  display: grid;
  row-gap: 30px;
  height: 1365px;
  width: 100%;
  max-width: 270px;
  overflow: hidden;

  @include tablet {
    max-width: 720px;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    margin-bottom: 40px;
  }

  @include desktop {
    max-width: none;
    height: auto;
    grid-template-columns: repeat(4, auto);
    padding: 15px 0;
    margin-bottom: 44px;
  }

}

.pets__item {
  transition: opacity 0.5s ease-in-out;
}

.pagination {
  display: flex;
  column-gap: 10px;

  @include tablet {
    margin-bottom: 6px;
    column-gap: 20px;
  }

  @include desktop {
    margin-bottom: 0;
  }
}

.pagination__button {
  width: 52px;
  height: 52px;

  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  border: 2px solid $desert_sand;

  &--inactive {
    color: $light_telegray;
    border-color: $light_telegray;
    cursor: auto;
  }

  &--current {
    background-color: $desert_sand;
    cursor: auto;
  }

  &--active:hover {
    border-color: $apricot_criola;
    background-color: $apricot_criola;
  }
}
